.header__login,
.footer__login{
  flex-shrink: 0;
  display: flex;
  align-items: center;
  font-weight: 500;
  &-icon{
    width: 2.8125em;
    margin-left: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &-title{
    font-size: 1em;
    line-height: 160%;
    letter-spacing: -.03em;
  }
}

.header{
  padding-top: 2.5em;
  &__inner{
    display: flex;
    align-items: center;
    border-radius: 6.25em;
    border: 2px solid $black;
    position: relative;
  }
  &__login{
    padding: 0 .56em 0 2.12em;
    border-radius: 6.25em;
    background-color: $black;
    height: 3.75em;
    color: #fff;
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  margin-right: 2.48em;
  padding-left: 1.75em;
  &__links{
    display: flex;
    br{
      display: none;
    }
  }
  &__link{
    font-weight: 600;
    height: 2.6875em;
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-right: 1.875em;
      padding-right: 1.875em;
      border-right: 2px solid $black;
    }
  }
}

.burger{
  display: none;
}

.lang-select{
  &._active{
    .lang-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    font-weight: 500;
    display: flex;
    align-items: center;
    transition: all .3s;
    svg{
      margin-left: .375em;
      width: .625em;
    }
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
  &__flag{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: .83em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__current{
    font-size: 1em;
    line-height: 160%;
    letter-spacing: -.03em;
  }
}

.lang-dropdown{
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: .75em;
  width: 25.625em;
  border-radius: 1.25em;
  background-color: #fff;
  box-shadow: -2px 7px 17px 0px rgba(0, 0, 0, 0.10), -9px 29px 31px 0px rgba(0, 0, 0, 0.09), -20px 66px 41px 0px rgba(0, 0, 0, 0.05), -35px 117px 49px 0px rgba(0, 0, 0, 0.01), -55px 183px 54px 0px rgba(0, 0, 0, 0.00);
  padding: 1.375em 2em;
  z-index: 5;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 600;
  }
  &__close{
    width: 1.25em;
    margin-left: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__items{
    margin-top: .94em;
    column-count: 3;
    column-gap: 1em;
  }
  &__item{
    display: inline-flex;
    align-items: center;
    width: 100%;
    @include lastM(0 0 .94em);
    img{
      width: 1.25em;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
      line-height: 140%;
      letter-spacing: -.03em;
    }
    &._active{
      font-weight: 600;
      color: $primary;
    }
  }
}


@media screen and (max-width: 1280px){
  .menu{
    padding-left: 1.75em;
    margin-right: 1.86em;
  }

  .lang-dropdown{
    width: 23.4375em;
    padding: 1.13em 1.19em;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 1.875em;
    &__login{
      padding: 0 .56em 0 1.625em;
      height: 3.125em;
      &-icon{
        width: 2.1875em;
      }
    }
  }

  .menu{
    margin-right: 1.56em;
    padding-left: 1.25em;
    &__links{
      br{
        display: unset;
      }
    }
    &__link{
      height: 2.125em;
      &:not(:last-child){
        margin-right: 1.25em;
        padding-right: 1.25em;
      }
      .text16{
        font-size: .875em;
        line-height: 130%;
      }
    }
  }

  .lang-select{
    &__flag{
      width: 2.1875em;
      margin-right: .94em;
    }
  }

  .lang-dropdown{
    width: 24.375em;
    padding: 1.44em 1.625em;
    margin-top: .625em;
    &__close{
      width: 1.875em;
    }
  }
}


@media screen and (max-width: 768px){
  .header{
    padding-top: 1.375em;
    &__inner{
      border: none;
    }
    &__login{
      padding: 0 .31em 0 1.3125em;
      height: 2.8125em;
      &-icon{
        margin-left: .81em;
      }
    }
    &._open{
      .menu,
      .menu-tablet{
        opacity: 1;
        pointer-events: unset;
      }
      .menu__bg{
        display: block;
      }
      .burger{
        rect{
          fill: $primary;
        }
      }
    }
  }

  .menu{
    justify-content: flex-end;
    &__bg{
      display: none;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background-color: rgba($black,.7);
      z-index: 3;
    }
    &__links{
      display: block;
    }
    &__link{
      height: 2.125em;
      &:not(:last-child){
        margin: 0 0 .94em;
        padding: 0 0 .94em;
        border-right: none;
        border-bottom: 2px solid $black;
      }
      .text16{
        font-size: .875em;
        line-height: 130%;
      }
    }
  }

  .menu-tablet{
    position: fixed;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    top: 0;
    left: 0;
    width: 23.125em;
    padding: 5.75em 1.25em 1em;
    border-radius: 0.625em;
    background-color: #fff;
    box-shadow: -2px 7px 17px 0px rgba(0, 0, 0, 0.10), -9px 29px 31px 0px rgba(0, 0, 0, 0.09), -20px 66px 41px 0px rgba(0, 0, 0, 0.05), -35px 117px 49px 0px rgba(0, 0, 0, 0.01), -55px 183px 54px 0px rgba(0, 0, 0, 0.00);
    z-index: 10;
  }

  .burger{
    z-index: 11;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 2.8125em;
    @include center;
    svg{
      width: 100%;
    }
  }
}


