.footer{
  padding: 7.5em 0 3.75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__action{
    display: flex;
    align-items: center;
  }
  &__title{
    br{
      display: none;
    }
  }
  .lang-select{
    position: relative;
  }
  .lang-dropdown{
    top: unset;
    bottom: 100%;
    margin: 0 0 .625em;
  }
  &__login{
    margin-left: 2.44em;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.875em;
  }
  &__links{
    display: flex;
    a{
      text-decoration-line: underline;
    }
  }
  &__item{
    font-size: 1em;
    font-weight: 500;
    line-height: 160%;
    letter-spacing: -0.03em;
    margin-right: 2.81em;
  }
}

.socials{
  display: flex;
  &__item{
    width: 3.125em;
    flex-shrink: 0;
    @include lastM(0 1.56em 0 0);
    svg{
      width: 100%;
    }
    &:hover{
      circle{
        fill: $primary;
      }
      path{
        fill: #fff;
      }
    }
  }
}


@media screen and (max-width: 1280px){

}


@media screen and (max-width: 992px){
  .footer{
    &__login{
      margin-left: 3.37em;
      &-icon{
        width: 2.5em;
        margin-left: .94em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
    }
    &__links{
      flex-wrap: wrap;
      margin-top: -.625em;
      max-width: 28em;
      ._order-1{
        order: -1;
      }
    }
    &__item{
      margin: .625em .625em 0 0;
    }
  }
}


@media screen and (max-width: 768px){
  .footer{
    &__login{
      margin-left: 2.75em;
      &-icon{
        width: 2.1875em;
        margin-left: .81em;
      }
    }
  }

  .socials{
    &__item{
      width: 2.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 6.25em 0 3.125em;
    &__top{
      display: block;
    }
    &__action{
      margin-top: 1.875em;
      display: flex;
      align-items: center;
    }
    &__title{
      br{
        display: unset;
      }
    }
    .lang-select{
      margin: 0;
    }
    .lang-dropdown{
      right: unset;
      left: 0;
    }
    &__login{
      margin-left: 3.125em;
      &-icon{
        width: 1.875em;
        margin-left: .69em;
      }
    }
    &__bottom{
      margin-top: 1.875em;
      display: block;
    }
    &__links{
      max-width: 17.875em;
      ._order-1-mob{
        order: -1;
      }
    }
  }

  .socials{
    margin-top: 1.25em;
    &__item{
      width: 3.875em;
      @include lastM(0 2.125em 0 0);
    }
  }
}
